import React, { useEffect, useLayoutEffect, useRef, useState } from "react";
import Techno from "./Techno";
import { useInView } from "react-intersection-observer";

function Projet({
  titreProjet,
  sousTitre,
  motsTechno,
  dive,
  handleOver,
  blocProjet,
  lien
}) {
  const projetRef = useRef([]);
  const [isVisible, setIsVisible] = useState(false);
  const [containerRef, inView, entry] = useInView({
    triggerOnce: true,
    threshold: 0.1,
  });

  useLayoutEffect(() => {
    if (window.innerWidth >= 1280) {
      let mouseX = 0,
        mouseY = 0;
      let translateX = 0,
        translateY = 0;
      let isMouseOver = false;
      let animationId;

      const handleMouseEnter = (e) => {
        isMouseOver = true;
        dive.current.style.display = "block";
        dive.current.style.transformOrigin = "center center";

        var rect = blocProjet.getBoundingClientRect();
        mouseX = e.clientX - rect.left;
        mouseY = e.clientY - rect.top;
        translateX = mouseX;
        translateY = mouseY;

        dive.current.style.transform = `translate(${translateX}px, ${translateY}px))`;

        animate();
      };

      const handleMouseMove = (e) => {
        if (isMouseOver) {
          var rect = blocProjet.getBoundingClientRect();
          mouseX = e.clientX - rect.left;
          mouseY = e.clientY - rect.top;
        }
      };

      const handleMouseLeave = () => {
        isMouseOver = false;
        dive.current.style.display = "none";

        if (animationId) {
          cancelAnimationFrame(animationId);
        }
      };

      function animate() {
        if (isMouseOver) {
          translateX += (mouseX - translateX) * 0.1;
          translateY += (mouseY - translateY) * 0.1;
          dive.current.style.transform = `translate(${translateX}px, ${translateY}px)`;
        } else {
          dive.current.style.transition = "none";
        }

        animationId = requestAnimationFrame(animate);
      }

      blocProjet.addEventListener("mouseenter", handleMouseEnter);
      blocProjet.addEventListener("mousemove", handleMouseMove);
      blocProjet.addEventListener("mouseleave", handleMouseLeave);

      return () => {
        blocProjet.removeEventListener("mouseenter", handleMouseEnter);
        blocProjet.removeEventListener("mousemove", handleMouseMove);
        blocProjet.removeEventListener("mouseleave", handleMouseLeave);

        if (animationId) {
          cancelAnimationFrame(animationId);
        }
      };
    }
  }, []);

  useEffect(() => {
    if (inView && entry && entry.boundingClientRect.y > 0) {
      setIsVisible(true);
    } else {
      setIsVisible(false);
    }
  }, [inView, entry]);

  return (
    <a
      ref={containerRef}
      href={lien}
      target='_blank'
      className={isVisible ? "visibleProjet" : "hiddenProjet"}
      onMouseEnter={(e) => handleOver(e)}
      style={{color:'white',textDecoration:'none'}}
    >
      <div className="projets">
        <div className="sousTitreProjet">
          <p>{titreProjet}</p>
          <span>{sousTitre}</span>
        </div>
        <div className="sousTitreProjet2">
          {motsTechno.map((x) => (
            <Techno mots={x} />
          ))}
        </div>
      </div>
      <div
        ref={projetRef}
        className="hoverProject"
        style={{ width: "100%", height: "0.05rem", transform: "scaleX(1)" }}
      ></div>
    </a>
  );
}

export default Projet;
